export default {
	data() {
		return {
			nurseInfo: {},
			noticeList: []
		}
	},
	onLoad() {
		this.nurseInfo = uni.getStorageSync('userInfo')
		this.fetchNoticeList()
	},
	methods: {
		// 跳转公告信息页
		toNotice(notice_id) {
			uni.navigateTo({
				url: `../notice/notice?id=${notice_id}`
			})
		},
		// 获取公告列表
		fetchNoticeList() {
			uni.request({
				url: 'https://ciai.le-cx.com/api/notice/noticeList',
				success: res => {
					// console.log(res)
					this.noticeList = res.data.data
				}
			})
		}
	}
}
